import React from 'react'
import {Route,Redirect,withRouter} from 'react-router-dom'

const PrivateRoute = ({component:Com, ...rest}) => {
  return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest}/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}

export default withRouter(PrivateRoute)
